body {
  color: #fff;
  background-color: #23272f;
  flex-direction: column;
  align-items: center;
  display: flex;
}

h1 {
  text-align: center;
}

.board {
  cursor: pointer;
  grid-template-rows: 100px 100px 100px;
  grid-template-columns: 100px 100px 100px;
  display: grid;
  position: relative;
}

.tile {
  justify-content: center;
  align-items: center;
  font-size: 2em;
  display: flex;
}

.right-border {
  border-right: 3.2px solid #0074a6;
}

.bottom-border {
  border-bottom: 3.2px solid #0074a6;
}

.left-border {
  border-left: 3.2px solid #0074a6;
}

.top-border {
  border-top: 3.2px solid #0074a6;
}

.X-hover:hover:after {
  content: "X";
  opacity: .4;
}

.O-hover:hover:after {
  content: "O";
  opacity: .4;
}

.strike {
  background-color: #adff2f;
  position: absolute;
}

.strike-r1 {
  width: 100%;
  height: 4px;
  top: 15%;
}

.strike-r2 {
  width: 100%;
  height: 4px;
  top: 48%;
}

.strike-r3 {
  width: 100%;
  height: 4px;
  top: 81%;
}

.strike-c1 {
  width: 4px;
  height: 100%;
  left: 15%;
}

.strike-c2 {
  width: 4px;
  height: 100%;
  left: 49%;
}

.strike-c3 {
  width: 4px;
  height: 100%;
  left: 81%;
}

.strike-d1 {
  width: 98%;
  height: 4px;
  top: 48%;
  transform: skewY(45deg);
}

.strike-d2 {
  width: 98%;
  height: 4px;
  top: 48%;
  transform: skewY(-45deg);
}

.game-over {
  text-align: center;
  border: dotted #0df;
  margin-top: 50px;
  padding: 10px;
  font-size: 1.5em;
}

.reset-btn {
  color: #fff;
  cursor: pointer;
  background-color: #098bab;
  width: 100%;
  margin-top: 50px;
  padding: 20px;
  font-size: 1.5em;
}
